import React from "react"; 
import './main_page.css';

function Header() {
    return (
        <div className="header">
            <div style={title}>เมนูวันนี้</div>
        </div>
    );
}

export default Header;

const title = {
    fontFamily: "sans-serif",
    fontSize: 30,
    color: 'white',
    fontWeight: "bold",
    textAlign: 'center'
  }